footer {
  padding: 20px 0;
  & .container {
    @include breakpoint-down(medium) {
      flex-direction: column;
      gap: 20px;
    }
    & .creds {
      text-align: center;
    }
    & .links {
      & a:not(:last-of-type) {
        margin-right: 15px;
        @include breakpoint-down(large) {
          margin-right: 5px;
        }
        @include breakpoint-down(medium) {
          margin-right: 7px;
        }
      }
    }
    & .socials {
      & * {
        cursor: pointer;
        &:not(:last-of-type) {
          margin-right: 15px;
          @include breakpoint-down(large) {
            margin-right: 4px;
          }
        }
      }
    }
  }
}
